<template>
  <!--个人信息组件-->
  <div class="profile">
    <header id="wx-header">
      <div class="center">
        <router-link to="/self" tag="div" class="iconfont icon-return-arrow">
          <span>我</span>
        </router-link>
        <span>个人信息</span>
      </div>
    </header>
    <div class="weui-cells">
      <div class="weui-cell" id="avatarCell">
        <!-- <van-uploader accept="image/png, image/jpeg, image/jpg" :after-read="afterRead"> -->
        <div class="weui-cell__bd">
          <p>头像</p>
        </div>
        <div class="weui-cell__ft">
          <van-uploader accept="image/png, image/jpeg, image/jpg" :after-read="afterRead">
            <img
              :src="'/api/file/showImg?fileUrl='+userInfo.headerUrl"
              style="width: 50px;height: 50px;border-radius: 4px;"
            />
          </van-uploader>
        </div>
        <!-- </van-uploader> -->
      </div>
      <div class="weui-cell" @click="changeName">
        <div class="weui-cell__bd">
          <p>名字</p>
        </div>
        <div class="weui-cell__ft">{{userInfo.nickname}}</div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__bd">
          <p>工号</p>
        </div>
        <div class="weui-cell__ft">{{userInfo.userId}}</div>
      </div>
      <router-link to="/self/profile/my-qrcode" class="weui-cell weui-cell_access">
        <div class="weui-cell__bd">
          <p>我的二维码</p>
        </div>
        <div class="weui-cell__ft">
          <img
            src="../../assets/images/contact_add-friend-my-qr.png"
            style="vertical-align: middle;;width:24px"
            class="_align-middle"
          />
        </div>
      </router-link>
      <div class="weui-cell">
        <div class="weui-cell__bd">
          <p>我的地址</p>
        </div>
        <div class="weui-cell__ft">
          <span v-for="item in userInfo.area">{{item}}&nbsp;&nbsp;&nbsp;</span>
        </div>
      </div>
    </div>

    <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__bd">
          <p>性别</p>
        </div>
        <div class="weui-cell__ft">{{userInfo.sex}}</div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__bd">
          <p>地区</p>
        </div>
        <div class="weui-cell__ft">
          <span v-for="item in userInfo.area">{{item}}&nbsp;&nbsp;&nbsp;</span>
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__bd">
          <p>个性签名</p>
        </div>
        <div class="weui-cell__ft">{{userInfo.signature}}</div>
      </div>
    </div>
    <!-- <van-action-sheet
      v-model="show"
      :actions="actions"
      cancel-text="取消"
      @cancel="onCancel"
      @select="onSelect"
    />-->

    <!-- <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__bd">
          <p>LinkedIn帐号</p>
        </div>
        <div class="weui-cell__ft">未设置</div>
      </div>
    </div>-->
  </div>
</template>
<script>
import { Uploader, ImagePreview, ActionSheet, Toast } from "vant";
import qs from "qs";
export default {
  data() {
    return {
      pageName: "个人信息",
      userInfo: ""
      // show: false,
      // actions: [
      //   { name: "拍照" },
      //   { name: "从手机相册选择" },
      //   { name: "查看头像" },
      //   { name: "保存图片" }
      // ]
    };
  },
  created() {
    // this.userInfo = JSON.parse(window.localStorage.getItem("userInfo"));
    this.user();
    console.log(this.$route.query.userId);
  },
  methods: {
    user() {
      this.$http
        .get("/api/user/userInfo?userId=" + this.$route.query.userId)
        .then(item => {
          if (item.data.code === 200) {
            console.log(item.data.data);
            localStorage.setItem("userInfo", JSON.stringify(item.data.data));
            this.userInfo = JSON.parse(window.localStorage.getItem("userInfo"));
            console.log(JSON.parse(window.localStorage.getItem("userInfo")));
          }
        });
    },
    //更改名称
    changeName() {
      // console.log(this.userInfo.nickname);
      this.$router.push({
        path: "/self/profile/change-self-name",
        query: {
          nickname: this.userInfo.nickname
        }
      });
    },
    // onCancel() {
    //   this.show = false;
    // },
    // onSelect(item) {
    //   this.show = false;
    //   if (item.name == "查看头像") {
    //     //预览图片
    //     ImagePreview({
    //       images: [this.userInfo.headerUrl]
    //     });
    //   } else if (item.name == "从手机相册选择") {
    //     console.log(888);
    //     // this.afterRead();
    //   }
    // },
    //更换头像
    afterRead(res) {
      let formData = new FormData();
      formData.append("file", res.file);

      this.$http
        .post("/api/file/upload/head", formData, {
          headers: {
            //添加请求头
            "Content-Type": "multipart/form-data"
          }
        })
        .then(res => {
          console.log(res.data.data[0].fileUrl);
          if (res.data.code == 200) {
            this.$http
              .post(
                "/api/set/user/editHeadPortrait",
                qs.stringify({
                  headerUrl: res.data.data[0].fileUrl
                })
              )
              .then(res => {
                if (res.data.code == 200) {
                  this.user();
                }
              });
          }
        });
    }
  }
};
</script>


